import { useState } from "react";
import GoogleMapReact from "google-map-react";
import GenericLocationMarker from "./markers/GenericLocationMarker";
import LocationInfoBox from "./LocationInfoBox";
import "../../css/Map/Map.css";
import { Icon } from "@iconify/react";
import {
  RiEarthquakeFill,
  AiOutlineFire,
  GiIceberg,
  GiFlood,
  IoThunderstorm,
} from "react-icons/all";
// define constants

const NATURAL_EVENT_SEVERE_STORMS = 10;
const NATURAL_EVENT_EARTHQUAKES = 16;

const NATURAL_EVENT_REGISTRY = [
  { id: 8, emoji: <p>üî•</p> },
  { id: 9, emoji: <p>üåä</p> }, // Flood
  { id: 10, emoji: <p>‚õàÔ∏è</p> },
  { id: 12, emoji: <p>üåã</p> },
  { id: 15, emoji: <p>üßä</p> },
  { id: 16, emoji: <p>üè†</p> }, //earthquake
  { id: 17, emoji: <p>‚ùÑÔ∏è</p> },
  { id: 18, emoji: <p>üå°Ô∏è</p> },
];

const Map = ({ eventData, center, zoom }) => {
  const [locationInfo, setLocationInfo] = useState(null);

  const markers = eventData.map((ev, index) => {
    //switch statement for different events here, 8 = wildfires
    // 15 = icebergs
    //10 = Tropical Cyclones

    let id = ev.categories[0].id;

    let lat = ev.geometries[0].coordinates[1];
    let lng = ev.geometries[0].coordinates[0];

    if (isNaN(lat) || isNaN(lng)) {
      return null;
    }

    for (let i = 0; i < NATURAL_EVENT_REGISTRY.length; i++) {
      let event = NATURAL_EVENT_REGISTRY[i];
      if (id === event.id) {
        let onClick = () =>
          setLocationInfo({ id: ev.id, title: ev.title, emoji: event.emoji });
        return (
          <GenericLocationMarker
            text={event.emoji}
            key={index}
            lat={lat}
            lng={lng}
            onClick={onClick}
          />
        );
      }
    }

    return null;
  });

  return (
    <div className="map">
      <GoogleMapReact
        bootstrapURLKeys={{ key: "AIzaSyDgclrR8QqACLDYcgLjsLd1RIZV9-V8Bpc" }}
        defaultCenter={center}
        defaultZoom={zoom}
      >
        {markers}
      </GoogleMapReact>
      {locationInfo && <LocationInfoBox info={locationInfo} />}
      <div className="key-info">
        <h2>Event Key</h2>
        <div className="legend">
          <span>
            <input
              type="checkbox"
              className="checkmark"
              defaultChecked={true}
            />
            <p> üî• - Wildfires</p>
          </span>
          <span>
            <input
              type="checkbox"
              className="checkmark"
              defaultChecked={true}
            />
            <p> üåä - Floods</p>
          </span>
          <span>
            <input
              type="checkbox"
              className="checkmark"
              defaultChecked={true}
            />
            <p> ‚õàÔ∏è - Severe Storms</p>
          </span>
          <span>
            <input
              type="checkbox"
              className="checkmark"
              defaultChecked={true}
            />
            <p> üåã - Volcanoes</p>
          </span>
          <span>
            <input
              type="checkbox"
              className="checkmark"
              defaultChecked={true}
            />
            <p> üßä - Icebergs</p>
          </span>
          <span>
            <input
              type="checkbox"
              className="checkmark"
              defaultChecked={true}
            />
            <p> üè† - Earthquakes</p>
          </span>
          <span>
            <input
              type="checkbox"
              className="checkmark"
              defaultChecked={true}
            />
            <p> ‚ùÑÔ∏è - Winter Storms</p>
          </span>
          <span>
            <input
              type="checkbox"
              className="checkmark"
              defaultChecked={true}
            />
            <p> üå°Ô∏è - Heat Waves</p>
          </span>
        </div>
      </div>
    </div>
  );
};

Map.defaultProps = {
  center: {
    lat: 39.9612,
    lng: -82.9988,
  },
  zoom: 6,
};

export default Map;
